@keyframes moveInLeft {
    0% {
        opacity: 0;
        transform: translateX(-10rem);
    }
 
    80% {
        transform: translate(1rem);
    }
 
    100% {
        opacity: 1;
        transform: translate(0);
    }
 }
 
 @keyframes moveInRight {
    0% {
        opacity: 0;
        transform: translateX(10rem);
    }
 
    80% {
        transform: translate(-1rem);
    } 
 
    100% {
        opacity: 1;
        transform: translate(0);
    }
 }
 
 @keyframes moveInBottom {
    0% {
        opacity: 0;
        transform: translatey(1.5rem);
    }
 
    100% {
        opacity: 1;
        transform: translate(0);
    }
 }
 