.card {
    perspective: 150rem;
    -moz-perspective: #fff;
    position: relative;
height: 52rem;

    &__side {
        height: 52rem;
        transition: all .8s ease;
        position: absolute; 
        top: 0;
        left: 0;
        width: 100%;
        backface-visibility: hidden;
        border-radius: 3px;
        overflow: hidden;
        box-shadow: 0 1.5rem 4rem rgba($color-black, 1.5);

        &--front {
            background-image: $color-white;
        }

        &--back {
            transform: rotateY(180deg);

            &-1 {
                background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark);
            }

            &-2 {
                background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);
            }

            &-3 {
                background-image: linear-gradient(to right bottom, $color-tertiary-light, $color-tertiary-dark);
            }
        }
    }


    &:hover &__side--front {
        transform: rotateY(-180deg);
    }

    &:hover &__side--back {
        transform: rotateY(0);
    }

    //FRONT SIDE STYLING
    &__picture {
        background-size: cover;
        height: 23rem;
        background-blend-mode: screen;
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
        clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;

        &--1 {
            background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark), 
            url(../images/nat-5.PNG);
        }

        &--2 {
            background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark), 
            url(../images/nat-6.PNG);
        }

        &--3 {
            background-image: linear-gradient(to right bottom, $color-tertiary-light, $color-tertiary-dark), 
            url(../images/nat-7.PNG);
        }
    }

    &__heading {
        font-size: 2.8rem;
        font-weight: 300;
        text-align: right;
        text-transform: uppercase;
        color: $color-white;
        position: absolute;
        top: 12rem;
        right: 2rem;
        width: 75%;
    }

    &__heading-span {
        padding: 1rem 1.5rem;
        -webkit-box-decoration-break: clone;
        box-decoration-break: clone;

        &--1 {
            background-image: linear-gradient(to right bottom, 
            rgba($color-secondary-light, .85), 
            rgba($color-secondary-dark, .85));

        }

        &--2 {
            background-image: linear-gradient(to right bottom, 
            rgba($color-primary-light, .85), 
            rgba($color-primary-dark, .85));

        }

        &--3 {
            background-image: linear-gradient(to right bottom, 
            rgba($color-tertiary-light, .85), 
            rgba($color-tertiary-dark, .85));

        }
    }

    &__details {
        padding: 3rem;
        // margin-top: -3rem;

        ul {
            list-style-type: none;
            width: 80%;
            margin: 0 auto;

            li {
                text-align: center;
                font-size: 1.5rem;
                padding: 1rem;

                &:not(:last-child) {
                    border-bottom: 1px solid $color-grey-light-2;
                }
            }
        }
    }

    //BACK SIDE STYLING
    &__cta {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 90%;
        text-align: center;

    }

    &__price-box {
        text-align: center;
        color: $color-white;
        margin-bottom: 8rem;
        
    }

    &__price-only {
        font-size: 1.4rem;
        text-transform: uppercase;
    }

    &__price-value {
        font-size: 6rem;
        font-weight: 50;
        font-family: inherit;
    }

    @include respond(tab-port) {
    height: auto;

    &__side {
        height: auto;
        transition: all .8s ease;
        position: relative;
        border-radius: 3px;

        &--back {
            transform: rotateY(0);

            // clip-path: polygon(0 15%, 100% 0, 100% 100%, 0% 100%);
        }
    }

    &:hover &__side--front {
        transform: rotateY(0);
    }

    &__details {
        padding: 3rem 3rem;
    }

    //BACK SIDE STYLING
    &__cta {
        position: relative;
        top: 0;
        left: 0%;
        transform: translateY(-4rem);
        width: 100%;
        padding: 4rem;
    }

    &__price-only {
        margin-top: 3rem;
    }

    &__price-box {
        margin-bottom: 3rem;  
    }

    &__price-value {
        font-size: 4rem
    }
    }
}